<template>
  <el-dialog
    :title="$t('personalCenter.TitleAddBankCard')"
    :visible.sync="visible"
    width="30%"
    label-width="100px"
  >
    <el-form
      label-position="right"
      label-width="100px"
      ref="addBankCardForm"
      :model="form"
    >
      <el-form-item
        :label="$t('personalCenter.bank')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-select
          v-model="form.bank"
          :placeholder="$t('personalCenter.selectBank')"
        >
          <el-option
            v-for="item in banks"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.area')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-cascader
          size="medium"
          v-model="form.area"
          :options="provinces"
        ></el-cascader>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.bankBranch')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-input v-model="form.branch" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.accountName')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-input v-model="form.accountName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.bankCardNumber')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-input v-model="form.cardNumber" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.confirmNumber')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-input v-model="form.accountNumber" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.fundPassword')"
        :rules="[
          {
            required: true,
            message: $t('personalCenter.requireInput'),
            trigger: 'blur'
          }
        ]"
      >
        <el-input
          type="password"
          v-model="form.fundPassword"
          autocomplete="off"
        ></el-input>
      </el-form-item>
    </el-form>
    <label v-if="serverErrorInfo !== ''" class="server-error">{{
      serverErrorInfo
    }}</label>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="addBankCard('addBankCardForm')">{{
        $t("alertComponent.confirm")
      }}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import {
  getDistricts,
  getBankList
} from "../../../api/userCenter/personalCenter/personal-center";
import { mapActions } from "vuex";

export default {
  name: "add-bank-card",
  props: ["checkedToken"],
  components: {},
  data() {
    return {
      provinces: [],
      serverErrorInfo: "",
      visible: false,
      banks: [],
      form: {
        fundPassword: "",
        accountNumber: "",
        cardNumber: "",
        accountName: "",
        branch: "",
        area: "",
        bank: ""
      }
    };
  },
  methods: {
    ...mapActions(["bindBankCard"]),
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    addBankCard(formName) {
      //TODO:成功时应当在顶部显示可自动消失的信息框
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.bindBankCard({
            bankId: this.form.bank,
            provinceId: this.form.area[0],
            cityId: this.form.area[1],
            accountName: this.form.accountName,
            account: this.form.cardNumber,
            branch: this.form.branch,
            fundPwd: this.form.fundPassword,
            checkedToken: this.checkedToken
          })
            .then(data => {
              this.visible = false;
            })
            .catch(err => {
              this.serverErrorInfo = err;
            });
        }
      });
    }
  },
  created() {
    //获取区域列表
    getDistricts().then(data => {
      if (data.errno === 0) {
        data = data.data;
        var provinceList = [];
        for (const prop in data) {
          const item = data[prop];
          let province = {};
          province.label = item.name;
          province.value = item.id;
          province.children = item.children.map(inner => {
            return { label: inner.name, value: inner.id };
          });
          provinceList.push(province);
        }
      }
      this.provinces = provinceList;
    });
    //获取银行卡列表
    getBankList().then(data => {
      if (data.errno === 0) {
        data = data.data;
        this.banks = data;
      }
    });
  }
};
</script>
