<template>
  <el-dialog
    class="change-password-dialog"
    :title="title"
    :visible.sync="dialogVisible"
    width="30%"
  >
    <el-form
      label-position="right"
      label-width="100px"
      :model="changePasswordForm"
      ref="changePasswordForm"
      :rules="rules"
    >
      <el-form-item
        :label="$t('personalCenter.oldPassword')"
        prop="oldPassword"
        v-if="showOldPassword"
      >
        <el-input
          type="password"
          v-model="changePasswordForm.oldPassword"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.newPassword')"
        prop="newPassword"
      >
        <el-input
          type="password"
          v-model="changePasswordForm.newPassword"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('personalCenter.confirmNewPassword')"
        prop="confirmPassword"
      >
        <el-input
          type="password"
          v-model="changePasswordForm.confirmPassword"
          autocomplete="off"
        ></el-input>
      </el-form-item>
    </el-form>
    <label v-if="serverErrorInfo !== ''" class="server-error">{{
      serverErrorInfo
    }}</label>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="changePassword('changePasswordForm')">{{
        $t("alertComponent.confirm")
      }}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapGetters } from "vuex";
import {
  changeLoginPwd,
  changeFundPwd
} from "../../../api/userCenter/personalCenter/personal-center";

export default {
  props: ["title", "type"],
  data() {
    var passwordValidator = (rule, val, callback) => {
      const value = this.changePasswordForm.oldPassword;
      if (!value || value.length < 6 || value.length > 16) {
        callback(new Error(this.$t("validateError.PasswordLength")));
      } else {
        if (this.changePasswordForm.confirmPassword !== "") {
          this.$refs.changePasswordForm.validateField("confirmPassword");
        }
        callback();
      }
    };
    var passwordValidator1 = (rule, val, callback) => {
      const value = this.changePasswordForm.newPassword;
      if (!value || value.length < 6 || value.length > 16) {
        callback(new Error(this.$t("validateError.PasswordLength")));
      } else {
        if (this.changePasswordForm.confirmPassword !== "") {
          this.$refs.changePasswordForm.validateField("confirmPassword");
        }
        callback();
      }
    };
    var passwordValidator2 = (rule, val, callback) => {
      const value = this.changePasswordForm.confirmPassword;
      if (value === "" || value.length < 6 || value.length > 16) {
        callback(new Error(this.$t("validateError.PasswordLength")));
      } else if (value !== this.changePasswordForm.newPassword) {
        callback(new Error(this.$t("validateError.PasswordDiff")));
      } else {
        callback();
      }
    };

    return {
      dialogVisible: false,
      serverErrorInfo: "",
      changePasswordForm: {
        oldPassword: "",
        newPassword: "",
        confirmPassword: ""
      },
      rules: {
        oldPassword: [
          {
            validator: passwordValidator,
            trigger: "blur"
          }
        ],
        newPassword: [
          {
            validator: passwordValidator1,
            trigger: "blur"
          }
        ],
        confirmPassword: [
          {
            validator: passwordValidator2,
            trigger: "blur"
          }
        ]
      }
    };
  },
  computed: {
    ...mapGetters(["userInfo"]),
    showOldPassword() {
      if (this.isChangeLoginPwdDlg) return true;
      if (this.isChangeFundPwdDlg && this.hasFundPassword) return true;
      return false;
    },
    hasFundPassword() {
      return this.userInfo.fund_pwd_seted === 1;
    },
    isChangeLoginPwdDlg() {
      return this.type === "login" || !this.type;
    },
    isChangeFundPwdDlg() {
      return this.type === "fund";
    }
  },
  methods: {
    show() {
      this.dialogVisible = true;
    },
    hide() {
      this.dialogVisible = false;
    },
    changePassword(formName) {
      console.log(this.type, "type");
      this.serverErrorInfo = "";
      if (this.isChangeLoginPwdDlg) {
        this.changeLoginPassword(formName);
      } else if (this.isChangeFundPwdDlg) {
        this.changeFundPassword(formName);
      }
    },
    changeLoginPassword(formName) {
      console.log("change login password");
      this.$refs[formName].validate(valid => {
        if (valid) {
          changeLoginPwd(
            this.changePasswordForm.oldPassword,
            this.changePasswordForm.newPassword
          ).then(data => {
            if (data.errno !== 0) {
              this.$message({
                type: "error",
                message: data.message
              });
            } else {
              this.dialogVisible = false;
            }
          });
        }
      });
    },
    changeFundPassword(formName) {
      console.log("change fund password");
      //if (!this.hasFundPassword) this.oldPassword = "N/A";
      this.$refs[formName].validate(valid => {
        if (valid) {
          changeFundPwd(
            this.changePasswordForm.oldPassword,
            this.changePasswordForm.newPassword
          ).then(data => {
            if (data.errno !== 0) {
              this.serverErrorInfo = data.message;
            } else {
              this.dialogVisible = false;
            }
          });
        }
      });
    }
  },
  created() {}
};
</script>
