<template>
  <el-dialog
    :title="$t('personalCenter.updateUserbase')"
    :visible.sync="visible"
    width="30%"
  >
    <el-form
      :model="form"
      label-width="100px"
      :rules="rules"
      ref="baseInfoForm"
    >
      <el-form-item :label="$t('personalCenter.name')" prop="name">
        <el-input v-model="form.name" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item :label="$t('personalCenter.email')" prop="email">
        <el-input v-model="form.email" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item :label="$t('personalCenter.skype')" prop="skype">
        <el-input v-model="form.skype" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item :label="$t('personalCenter.mobile')" prop="mobile">
        <el-input v-model="form.mobile" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item :label="$t('personalCenter.qq')" prop="qq">
        <el-input v-model="form.qq" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="updateUserInfo('baseInfoForm')">{{
        $t("alertComponent.confirm")
      }}</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { hide } from "../../../util/loading";
export default {
  data() {
    return {
      visible: false,
      form: {
        name: "",
        email: "",
        skype: "",
        mobile: "",
        qq: "",
        //TODO:以下几个参数无法从用户信息中获取，每次修改都会置零
        betConfirm: 0,
        openPromptVoice: 0,
        wonNoticeEnabled: 0,
        nickname: ""
      },
      rules: {
        name: [
          {
            reurire: true,
            message: this.$t("personalCenter.requireInput"),
            trigger: "blur"
          },
          {
            min: 1,
            max: 50,
            message: this.$t("validateError.ContentLength"),
            trigger: "blur"
          }
        ],
        email: [
          { type: "email", message: this.$t("validateError.mustEmailStyle") },
          {
            min: 1,
            max: 50,
            message: this.$t("validateError.ContentLength"),
            trigger: "blur"
          }
        ],
        skype: [
          {
            min: 1,
            max: 50,
            message: this.$t("validateError.ContentLength"),
            trigger: "blur"
          }
        ],
        mobile: [
          {
            min: 1,
            max: 50,
            message: this.$t("validateError.ContentLength"),
            trigger: "blur"
          }
        ],
        qq: [
          {
            min: 1,
            max: 50,
            message: this.$t("validateError.ContentLength"),
            trigger: "blur"
          }
        ]
      }
    };
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  methods: {
    ...mapActions(["setUserInfo"]),
    show() {
      this.visible = true;
    },
    hide() {
      this.visible = false;
    },
    updateUserInfo(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.form.nickname = this.userInfo.nickname;
          this.setUserInfo(this.form)
            .then(data => {
              this.hide();
            })
            .catch(err => {
              this.$message({
                type: "error",
                message: err
              });
            });
        }
      });
    },
    setInitialValue() {
      this.form.name = this.userInfo.name || "";
      this.form.email = this.userInfo.email || "";
      this.form.skype = this.userInfo.skype || "";
      this.form.mobile = this.userInfo.mobile || "";
      this.form.qq = this.userInfo.qq || "";
    }
  },
  created() {
    this.setInitialValue();
  }
};
</script>
