<template>
  <div class="top-base-info">
    <el-row>
      <el-col :span="6" class="avatar">
        <div class="avatar">
          <el-avatar :size="160" :src="icon"></el-avatar>
          <br />
          <el-button v-if="canShowAvatar" type="primary" round>{{
            $t("personalCenter.changeAvatar")
          }}</el-button>
        </div>
      </el-col>
      <el-col :span="18" class="info">
        <div class="info-list el-row">
          <div class="el-col el-col-2">{{ $t("personalCenter.Account") }}</div>
          <div class="el-col el-col-6">{{ userInfo.username }}</div>
        </div>
        <div class="info-list el-row">
          <div class="el-col el-col-2">{{ $t("personalCenter.Nickname") }}</div>
          <div class="el-col el-col-6">
            {{ userInfo.nickname }}
            <span
              ><a href="javascript:;" v-on:click="nickDialogVisible = true"
                >[{{ $t("personalCenter.Modify") }}]</a
              >
              <i class="tip">{{ $t("personalCenter.ModifyOnce") }}</i></span
            >
          </div>
          <div class="el-col el-col-2">{{ $t("personalCenter.Balance") }}</div>
          <div class="el-col el-col-6">
            <span>{{ userInfo.abalance }}</span>
          </div>
        </div>
        <div class="info-list el-row" style="margin-bottom: 20px;">
          <div class="el-col el-col-2">
            {{ $t("personalCenter.ReservedInfo") }}
          </div>
          <div class="el-col el-col-6">
            <span>{{ $t("personalCenter.NoReservedInfo") }}</span>
          </div>
        </div>
        <el-rate
          :colors="['#ff0000', '#ff0000', '#ff0000']"
          v-model="rate"
          disabled
        >
        </el-rate>
        <p class="title" v-if="value < 3">
          <span style="color: red;">{{
            $t("personalCenter.securityNotice")
          }}</span>
        </p>
        <el-button class="btn-plain" @click="showChangePasswordDialog()">{{
          $t("personalCenter.changePassword")
        }}</el-button>
        <change-password
          ref="changePasswordDialog"
          :title="$t('personalCenter.changePassword')"
        />
      </el-col>
    </el-row>
    <el-dialog
      :title="$t('personalCenter.Nickname')"
      :visible.sync="nickDialogVisible"
      width="30%"
    >
      <el-form ref="form" :rules="rules" :model="form" label-width="100px">
        <el-form-item :label="$t('personalCenter.Nickname')" prop="nickname">
          <el-input v-model="form.nickname"></el-input>
        </el-form-item>
        <el-form-item>
          <span v-if="serverError !== ''" class="server-error">{{
            serverError
          }}</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="updateNickName('form')">{{
          $t("alertComponent.confirm")
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import ChangePassword from "./change-password";
import { setUserInfo } from "../../../api/userCenter/personalCenter/personal-center";

export default {
  name: "top-base-info",
  props: ["rate"],
  components: {
    ChangePassword
  },
  data() {
    return {
      serverError: "",
      form: {
        nickname: ""
      },
      canShowAvatar: false, //TODO:暂不支持图像设置
      nickDialogVisible: false,
      icon: require("../../../common/img/avatar.jpg"),
      rules: {
        nickname: [
          {
            required: true,
            message: this.$t("personalCenter.requireInput"),
            trigger: "blur"
          }
        ]
      }
    };
  },
  computed: {
    ...mapGetters(["userInfo"]),
    value() {
      return this.userInfo.fund_pwd_seted === 1 ? 5 : 2.5;
    }
  },
  methods: {
    ...mapActions(["setUserNickname"]),
    showChangePasswordDialog() {
      this.$refs.changePasswordDialog.show();
    },
    updateNickName(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.setUserNickname(this.form.nickname)
            .then(data => {
              this.nickDialogVisible = false;
            })
            .catch(err => {
              this.$message({
                type: "error",
                message: err
              });
            });
        }
      });
    }
  }
};
</script>

<style lang="sass">
@import "../../../common/css/color.sass"

.top-base-info
    border-bottom: solid 1px #eee
    padding: 20px 10px
    .btn-plain
      margin-top: 30px
      padding-left: 0
    .avatar
        width: 180px
        display: flex
        align-items: center
        justify-content: center
        margin: auto
        flex-direction: column
        margin: 0 20px
    .info
        .el-rate
            i
                font-size: 40px
        .tip
            font-size: 12px
            color: #5e6d82
        a
        span
            color: var(--primary)
        .el-row
            margin: 10px 0px
        .title
            padding-top: 30px
            font-size: 16px
</style>
