<template>
  <div>
    <el-card class="box-card">
      <div :class="'infos ' + icon">
        <i :class="success ? 'el-icon-success' : 'el-icon-warning'"></i>
        <div class="title">{{ this.title }}</div>
        <div class="help">
          {{ this.content }}
        </div>
        <div class="actions">
          <a v-if="button1 !== undefined" v-on:click="buttonClick(1)">{{
            this.button1
          }}</a>
          <a v-on:click="buttonClick(0)">{{ this.button }}</a>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  props: [
    "success",
    "title",
    "content",
    "button",
    "icon",
    "button-event",
    "button1"
  ],
  data() {
    return {};
  },
  components: {},
  mounted() {},
  methods: {
    buttonClick(index) {
      this.buttonEvent(index);
    }
  },
  created() {}
};
</script>

<style lang="sass">
.personal-center
  .box-card
    &:hover
      border: solid 1px var(--primary)
    .icon1
      background-position: 0px -172px
    .icon2
      background-position: 0px -344px
    .icon3
      background-position: 0px -516px
    .icon4
      background-position: 0px -688px
    .icon5
      background-position: 0px -860px
    .icon6
      background-position: 0px -1032px
    .icon7
      background-position: 0px -1204px
    .icon8
      background-position: 0px -1376px
    .icon9
      background-position: 0px -2370px
.infos
  background-image: url("../static/img/manager-icon.png")
  height: 160px
  padding-left: 90px
  position: relative
  background-repeat: no-repeat
  i
    position: absolute
    top: 0
    left: 0
    font-size: 24px
    &.el-icon-success
      color: var(--success)
    &.el-icon-warning
      color: var(--primary)
  .title
   line-height: 32px
   color: var(--primary)
  .help
   line-height: 22px
   opacity: 0.7
  .actions
    line-height: 24px
    position: absolute
