<template>
  <div class="account-info">
    <top-base-info :rate="starValue" />
    <el-row :gutter="10" v-for="index in [0, 1, 2]" :key="index">
      <el-col
        v-for="item in cards.slice(index * 3, index * 3 + 3)"
        :key="item.title"
        :span="8"
      >
        <card
          :title="item.title"
          :content="item.content"
          :button="item.button"
          :button1="item.button1"
          :icon="item.icon"
          :success="item.success"
          :button-event="item.buttonEvent"
        />
      </el-col>
    </el-row>
    <change-password
      :type="changePasswordType"
      ref="changePasswordDialog"
      :title="$t('personalCenter.changePassword')"
    />
    <add-bank-card ref="addBankCard" :checked-token="checkedToken" />
    <base-info ref="baseInfo" />
  </div>
</template>

<script>
import TopBaseInfo from "./my-account/top-base-info.vue";
import Card from "./my-account/card.vue";
import { mapActions, mapGetters } from "vuex";

import ChangePassword from "./my-account/change-password";
import AddBankCard from "./my-account/add-bank-card";
import BaseInfo from "./my-account/base-info";
import router from "../../router";

export default {
  name: "myAccount",
  components: { TopBaseInfo, Card, ChangePassword, AddBankCard, BaseInfo },
  data() {
    var manageBankCardLabel = this.$t("personalCenter.ManageBankCard");
    if (this.bindingBankCard)
      manageBankCardLabel += `(${this.bindingBankCard.length})`;
    return {
      checkedToken: "",
      changePasswordType: "login"
    };
  },
  computed: {
    ...mapGetters(["bindingBankCard", "userInfo"]),
    starValue() {
      var success = this.cards.filter(p => p.success).length;
      var length = this.cards.length;

      console.log(success, length);
      return (success * 5) / length;
    },
    bindingBankCardLength() {
      if (this.bindingBankCard) return this.bindingBankCard.length;
      return 0;
    },
    cards() {
      return [
        {
          title: this.$t("personalCenter.TitleLoginPassword"),
          content: this.$t("personalCenter.ContentLoginPassword"),
          button: this.$t("personalCenter.ButtonLoginPassword"),
          icon: "icon",
          success: true,
          buttonEvent: this.changeLoginPassword
        },
        {
          title: this.$t("personalCenter.fundPassword"),
          content: this.$t("personalCenter.ContentLoginPassword"),
          button: this.$t("personalCenter.ButtonLoginPassword"),
          icon: "icon1",
          success: ((this.userInfo && this.userInfo.fund_pwd_seted) || 0) !== 0,
          buttonEvent: this.changeFundPassword
        },
        // {
        //   title: this.$t("personalCenter.TitleLoginPassword"),
        //   content: this.$t("personalCenter.ContentLoginPassword"),
        //   button: this.$t("personalCenter.ButtonLoginPassword"),
        //   icon: "icon2",
        //   success: true
        // },
        {
          title: this.$t("personalCenter.BindBankCard"),
          content: this.$t("personalCenter.BindBankCardNotice"),
          button: this.$t("personalCenter.AddBankCardBinding"),
          button1: this.$t("personalCenter.ManageBankCard"),
          icon: "icon3",
          success:
            ((this.bindingBankCard && this.bindingBankCard.length) || 0) !== 0,
          buttonEvent: this.addBankCard
        },
        // {
        //   title: this.$t("personalCenter.TitleLoginPassword"),
        //   content: this.$t("personalCenter.ContentLoginPassword"),
        //   button: this.$t("personalCenter.ButtonLoginPassword"),
        //   icon: "icon4",
        //   success: true
        // },
        {
          title: this.$t("personalCenter.baseInfo"),
          content: this.$t("personalCenter.baseInfoNotice"),
          button: this.$t("personalCenter.baseInfoButton"),
          icon: "icon9",
          success: ((this.userInfo && this.userInfo.name) || "") !== "",
          buttonEvent: this.modifyUserInfo
        }
      ];
    }
  },
  watch: {
    bindingBankCardLength(newVal, oldVal) {
      this.cards[2].button1 = `${this.$t(
        "personalCenter.ManageBankCard"
      )}(${newVal})`;
    }
  },
  methods: {
    ...mapActions(["updateUserInfo", "bindBankCard"]),
    changeLoginPassword() {
      this.changePasswordType = "login";
      this.$refs.changePasswordDialog.show();
    },
    changeFundPassword() {
      this.changePasswordType = "fund";
      this.$refs.changePasswordDialog.show();
    },
    addBankCard(index) {
      if (index === 0) {
        if (this.bindingBankCard && this.bindingBankCard.length > 0) {
          this.$eventHub
            .$off("check-bank-card-success")
            .$once("check-bank-card-success", data => {
              this.checkedToken = data.checkedToken;
              this.$refs.addBankCard.show();
            });
          this.$fundAlertCheckBankCard({ type: 1 });
        } else {
          this.$refs.addBankCard.show();
        }
      } else {
        router.push({ path: "/user/bank" });
      }
    },
    modifyUserInfo() {
      this.$refs["baseInfo"].show();
    }
  },
  created() {
    this.updateUserInfo();
  }
};
</script>

<style lang="sass">
.account-info
  border:solid 1px #fff
  .el-row
    margin: 20px 10px
</style>
